import { VIDGET_ADDITION_PARAMETERS } from '@screens/dashboard/components';

import { COLORS } from 'styles/colors';
import { getTransparent } from 'styles/utils';

export const getColor = (type: string) => {
  switch (type) {
    case VIDGET_ADDITION_PARAMETERS[1]:
      return {
        pointBorderColor: getTransparent(COLORS.primaryOrange, 0.8),
        borderColor: getTransparent(COLORS.primaryOrange, 0.7),
        backgroundColor: getTransparent(COLORS.primaryOrange, 0.2),
      };
    case VIDGET_ADDITION_PARAMETERS[2]:
      return {
        pointBorderColor: getTransparent(COLORS.primaryBlue, 0.8),
        borderColor: getTransparent(COLORS.primaryBlue, 0.7),
        backgroundColor: getTransparent(COLORS.primaryBlue, 0.2),
      };
    case VIDGET_ADDITION_PARAMETERS[3]:
      return {
        pointBorderColor: getTransparent(COLORS.primaryLime, 0.8),
        borderColor: getTransparent(COLORS.primaryLime, 0.7),
        backgroundColor: getTransparent(COLORS.primaryLime, 0.2),
      };
    case VIDGET_ADDITION_PARAMETERS[4]:
      return {
        pointBorderColor: getTransparent(COLORS.primaryRed, 0.8),
        borderColor: getTransparent(COLORS.primaryRed, 0.7),
        backgroundColor: 'rgba(0,0,0,0)',
      };
  }
};
