import { TUserActions } from './user.actions';

import { INIT_USER, IUserRedux, USER_ACTIONS } from './user.constants';

const { LOGIN, LOGOUT } = USER_ACTIONS;

export const reducerUser = (
  state = INIT_USER,
  action: TUserActions
): IUserRedux => {
  switch (action.type) {
    case LOGIN:
      return {
        user: action.payload,
      };

    case LOGOUT:
      return INIT_USER;

    default:
      return state ?? INIT_USER;
  }
};
