import { getPrice } from '@services/currency.api';
import { IUserInfo } from '@services/redux/admin/admin.constants';
import { Order } from './data-table.state';

import { Currency } from '@constants/currency';

export const getCompire = (
  order: Order,
  orderBy: number,
  currencyBase: Currency,
  convert: readonly [number, number]
) => (a: IRecord, b: IRecord) => {
  switch (orderBy) {
    case 0:
      return compireStrings(order, a.title, b.title);
    case 1:
      return compireStrings(order, a.description, b.description);
    case 2:
      return compireNumbers(
        order,
        getPrice(a.value, a.currency, currencyBase, convert),
        getPrice(b.value, b.currency, currencyBase, convert)
      );
    case 3:
      return compireNumbers(order, a.time, b.time);
    default:
      return 0;
  }
};

const compireNumbers = (order: Order, a: number, b: number) =>
  order === Order.ASC ? a - b : b - a;

const compireStrings = (order: Order, a: string, b: string) => {
  if (a < b) {
    return order === Order.ASC ? -1 : 1;
  }
  if (a > b) {
    return order === Order.ASC ? 1 : -1;
  }
  return 0;
};

export const getAdminCompire = (order: Order, orderBy: number) => (
  a: IUserInfo,
  b: IUserInfo
) => {
  switch (orderBy) {
    case 0:
      return compireStrings(order, a.email || '', b.email || '');
    case 1:
      return compireStrings(order, a.name || '', b.name || '');
    default:
      return 0;
  }
};
