import { Button, Card, CardContent } from '@material-ui/core';
import { ChartDataSets } from 'chart.js';
import { FC, useState } from 'react';
import { Line } from 'react-chartjs-2';

import { DialogPrompt } from '@components/dialog-modal';
import { Loader } from '@components/loader';
import { VIDGET_ADDITION_PARAMETERS } from '../new-vidget';

import { useAction, useReduxSelector } from '@services/redux';
import { VidgetActions } from '@services/redux/vidget';
import {
  getColor, getData, getLabels, getPrediction, getPredictLabels
} from '@services/vidgets';

import { PROMISES } from '@constants/promises';

import { useDashboardCardStyles } from './dashboard-card.styles';

interface IDashboardCard {
  vidget: IVidget;
  convert: readonly [number, number];
}

const VIDGETS_BASE_DATA = [
  VIDGET_ADDITION_PARAMETERS[1],
  VIDGET_ADDITION_PARAMETERS[2],
  VIDGET_ADDITION_PARAMETERS[3],
];

export const DashboardCard: FC<IDashboardCard> = ({ vidget, convert }) => {
  const classes = useDashboardCardStyles();

  const redux = useReduxSelector((redux) => redux);

  const del = useAction(VidgetActions.delete);

  const [isOpenDelete, setOpenDelete] = useState(false);

  const handleClose = () => setOpenDelete(false);
  const handleOpen = () => setOpenDelete(true);
  const handleDelete = () => {
    del(vidget.id);
    handleClose();
  };

  const getDatasets = () => {
    const datasets: ChartDataSets[] = [
      {
        label: vidget.base,
        data: getData(redux, vidget.base, convert, vidget.period),
        ...getColor(vidget.base),
      },
    ];

    if (VIDGETS_BASE_DATA.includes(vidget.additional)) {
      datasets.push({
        label: vidget.additional,
        data: getData(redux, vidget.additional, convert, vidget.period),
        ...getColor(vidget.additional),
      });
    }

    return datasets;
  };

  const getPredictionDatasets = () => {
    const dataset = getDatasets();

    dataset.push({
      label: vidget.additional,
      data: getPrediction(
        getData(redux, vidget.base, convert, vidget.period),
        vidget.period
      ),
      ...getColor(vidget.additional),
    });

    return dataset;
  };

  return (
    <Card className={classes.card} elevation={3}>
      <CardContent className={classes.cardContent}>
        <Loader id={PROMISES.PRICE} width={950} height={550}>
          {vidget.additional === VIDGET_ADDITION_PARAMETERS[4] ? (
            <Line
              data={{
                labels: getPredictLabels(vidget.period),
                datasets: getPredictionDatasets(),
              }}
            />
          ) : (
            <Line
              data={{
                labels: getLabels(vidget.period),
                datasets: getDatasets(),
              }}
            />
          )}
        </Loader>
        <div>
          <Button variant="contained" color="secondary" onClick={handleOpen}>
            Delete
          </Button>
        </div>
      </CardContent>
      <DialogPrompt
        isOpen={isOpenDelete}
        description="Are you sure you want to detele this vidget?"
        title="Delete vidget"
        handleClose={handleClose}
        handleSubmit={handleDelete}
      />
    </Card>
  );
};
