import { action, TActionCombiner } from '../utils';

import { USER_ACTIONS } from './user.constants';

const { LOGIN, LOGOUT } = USER_ACTIONS;

export const UserActions = {
  login: (user: IUser) => action(LOGIN, user),
  logout: () => action(LOGOUT),
};

export type TUserActions = ReturnType<TActionCombiner<typeof UserActions>>;
