import { TSettingActions } from './setting.actions';

import {
  INIT_SETTING, ISettingRedux, SETTING_ACTIONS
} from './setting.constants';

const { GET } = SETTING_ACTIONS;

export const reducerSetting = (
  state = INIT_SETTING,
  action: TSettingActions
): ISettingRedux => {
  switch (action.type) {
    case GET:
      return {
        settings: [...action.payload],
      };

    default:
      return state ?? INIT_SETTING;
  }
};
