import {
  Menu, MenuItem, Paper, Table, TableBody, TableContainer, TablePagination,
  Typography
} from '@material-ui/core';
import { FC } from 'react';
import { usePromiseTracker } from 'react-promise-tracker';

import { DialogPrompt } from '@components/dialog-modal';
import { Loader } from '@components/loader';
import { Select } from '../select';
import { DataRow, DataTableEdit, DataTableHead } from './components';

import { usePrice } from '@services/currency.api';
import { useDataTableState } from './data-table.state';
import { getCompire } from './data-table.utils';

import { CurrencyList } from '@constants/currency';
import { PROMISES } from '@constants/promises';

import { useDataTableStyles } from './data-table.styles';

interface IDataTable {
  data: IRecord[];
  handleDelete: (id: string) => void;
  handleEdit: (data: IRecord) => void;
  isIncome: boolean;
}

export const DataTable: FC<IDataTable> = ({
  data,
  handleDelete,
  handleEdit,
  isIncome,
}) => {
  const classes = useDataTableStyles();

  const {
    page,
    orderBy,
    order,
    rowsPerPage,
    getHandleHeaderClick,
    handleChangeRowsPerPage,
    handleChangePage,

    menuAnchorEl,
    getHandleOpenMenu,
    handleCloseMenu,

    handleOpenDelete,
    handleSubmitDelete,
    handleCancelDelete,
    isDeleteModal,

    handleOpenEdit,
    handleCloseEdit,
    isEditModal,

    getActiveRecord,

    currency,
    setCurrency,
  } = useDataTableState({ handleDelete });

  const convert = usePrice();

  const { promiseInProgress } = usePromiseTracker({
    area: PROMISES.USER,
  });

  return promiseInProgress ? (
    <Loader id={PROMISES.USER} />
  ) : (
    <Paper>
      <TableContainer>
        <Table
          className={classes.table}
          aria-labelledby="tableTitle"
          size="medium"
          aria-label="enhanced table"
        >
          <DataTableHead
            rowCount={data.length}
            numSelected={0}
            orderBy={orderBy}
            order={order}
            getHandleHeaderClick={getHandleHeaderClick}
          />
          <TableBody>
            {data
              .sort(getCompire(order, orderBy, currency, convert))
              .slice((page - 1) * rowsPerPage, page * rowsPerPage)
              .map((row) => (
                <DataRow
                  {...row}
                  currencyBase={currency}
                  getHandleOpenMenu={getHandleOpenMenu}
                  key={row.id}
                  convert={convert}
                />
              ))}
          </TableBody>
        </Table>
      </TableContainer>

      <div className={classes.tableFooter}>
        <div className={classes.currency}>
          <Typography>Default Currency</Typography>
          <div className={classes.currencySelect}>
            <Select
              list={CurrencyList}
              value={currency}
              setValue={setCurrency}
            />
          </div>
        </div>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          count={data.length}
          rowsPerPage={rowsPerPage}
          page={page - 1}
          component="div"
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      </div>
      <Menu
        id="data-table-menu"
        anchorEl={menuAnchorEl}
        keepMounted
        open={Boolean(menuAnchorEl)}
        onClose={handleCloseMenu}
      >
        <MenuItem onClick={handleOpenEdit}>Edit</MenuItem>
        <MenuItem onClick={handleOpenDelete}>Delete</MenuItem>
      </Menu>
      <DialogPrompt
        isOpen={isDeleteModal}
        description="This action will delete this record"
        title="Delete"
        handleClose={handleCancelDelete}
        handleSubmit={handleSubmitDelete}
      />
      <DataTableEdit
        handleCloseModal={handleCloseEdit}
        handleEdit={handleEdit}
        record={getActiveRecord(data)}
        isEditModal={isEditModal}
        isIncome={isIncome}
      />
    </Paper>
  );
};
