import { Button, TableCell, TableRow } from '@material-ui/core';
import { MoreHoriz } from '@material-ui/icons';
import { FC, MouseEvent } from 'react';

import { Loader } from '@components/loader';

import { getPrice } from '@services/currency.api';
import { getCurrency } from '@services/utils';

import { Currency } from '@constants/currency';
import { PROMISES } from '@constants/promises';

import { useDataTableStyles } from '../data-table.styles';

interface IDataRow extends IRecord {
  currencyBase: Currency;
  convert: readonly [number, number];
  getHandleOpenMenu: (
    id: string
  ) => (event: MouseEvent<HTMLButtonElement>) => void;
}

export const DataRow: FC<IDataRow> = ({
  id,
  title,
  description,
  value,
  currencyBase,
  getHandleOpenMenu,
  convert,
  time,
  currency,
}) => {
  const classes = useDataTableStyles();

  return (
    <TableRow hover tabIndex={-1} role="checkbox">
      <TableCell padding="checkbox">
        <Button
          aria-controls="data-table-menu"
          aria-haspopup="true"
          className={classes.button}
          onClick={getHandleOpenMenu(id)}
        >
          <MoreHoriz className={classes.buttonContent} />
        </Button>
      </TableCell>
      <TableCell component="th" scope="row" padding="none">
        {title}
      </TableCell>
      <TableCell align="left">{description}</TableCell>
      <TableCell align="right">
        <Loader id={PROMISES.PRICE} height={20} justify="flex-end" >
          {getPrice(value, currency, currencyBase, convert).toFixed(2)}{' '}
          {getCurrency(currencyBase)}
        </Loader>
      </TableCell>
      <TableCell align="right">
        {new Date(time).toLocaleTimeString('en-US')} -{' '}
        {new Date(time).toLocaleDateString('en-US')}
      </TableCell>
    </TableRow>
  );
};
