import {
  TableCell, TableHead, TableRow, TableSortLabel
} from '@material-ui/core';
import { FC } from 'react';

import { Order } from '../data-table.state';

interface IDataTableHead {
  numSelected: number;
  rowCount: number;
  orderBy: number;
  order: Order;
  getHandleHeaderClick: (id: number) => () => void;
}

interface IHeadCell {
  id: number;
  label: string;
  numeric: boolean;
  skipPadding?: boolean;
}

const headCells: IHeadCell[] = [
  { id: 0, label: 'Title', skipPadding: true, numeric: false },
  { id: 1, label: 'Description', numeric: false },
  { id: 2, label: 'Value', numeric: true },
  { id: 3, label: 'Date Added', numeric: true },
];

export const DataTableHead: FC<IDataTableHead> = ({
  orderBy,
  order,
  getHandleHeaderClick,
}) => (
  <TableHead>
    <TableRow>
      <TableCell padding="checkbox" />
      {headCells.map((headCell) => (
        <TableCell
          key={headCell.id}
          align={headCell.numeric ? 'right' : 'left'}
          padding={headCell.skipPadding ? 'none' : 'default'}
          onClick={getHandleHeaderClick(headCell.id)}
        >
          <TableSortLabel active={orderBy === headCell.id} direction={order}>
            {headCell.label}
          </TableSortLabel>
        </TableCell>
      ))}
    </TableRow>
  </TableHead>
);
