import { useCallback, useEffect, useState } from 'react';
import { trackPromise } from 'react-promise-tracker';

import { CONFIG } from '@constants/config';
import { Currency } from '@constants/currency';
import { PROMISES } from '@constants/promises';

export const usePrice = () => {
  const [uah, setUAH] = useState(0);
  const [eur, setEUR] = useState(0);

  const fetchPrice = useCallback(async () => {
    const convertUAH = (await (
      await fetch(`${CONFIG.CURRENCY_API}USD_UAH`)
    ).json()) as { USD_UAH: number };
    const convertEUR = (await (
      await fetch(`${CONFIG.CURRENCY_API}USD_EUR`)
    ).json()) as { USD_EUR: number };

    setUAH(convertUAH.USD_UAH);
    setEUR(convertEUR.USD_EUR);
  }, []);

  useEffect(() => {
    trackPromise(fetchPrice(), PROMISES.PRICE);
  }, [fetchPrice]);

  return <const>[uah, eur];
};

export const getPrice = (
  value: number,
  currencyFrom: Currency,
  currencyTo: Currency,
  convert: readonly [number, number]
) => {
  let price = value;

  switch (currencyFrom) {
    case Currency.UAH:
      price /= convert[0];
      break;
    case Currency.EUR:
      price /= convert[1];
      break;
  }

  switch (currencyTo) {
    case Currency.UAH:
      price *= convert[0];
      break;
    case Currency.EUR:
      price *= convert[1];
      break;
  }

  return price;
};
