import { TypedUseSelectorHook, useDispatch, useSelector } from 'react-redux';

import { IReduxStore } from './reducers';

export type TActionCombiner<T> = T extends { [key: string]: infer U }
  ? U
  : never;

export function action<T extends string, P>(
  type: T,
  payload: P
): { type: T; payload: P };

export function action<T extends string>(type: T): { type: T };

export function action<T extends string, P = undefined>(type: T, payload?: P) {
  return { type, payload } as any;
}

export const useReduxSelector: TypedUseSelectorHook<IReduxStore> = useSelector;

export const useAction = <T extends (...args: any[]) => any>(action: T) => {
  const dispatch = useDispatch();
  return (...args: Parameters<T>) => {
    dispatch(action(...args));
  };
};
