export const getDays = (days: number) =>
  new Array(days)
    .fill(1)
    .map((_, index) => {
      const day = new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * index);

      return new Date(day.getFullYear(), day.getMonth(), day.getDate());
    })
    .reverse();

export const getDaysOfWeek = (days: Date[]) =>
  days.map(
    (date) => ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'][date.getDay()]
  );

export const getPredictWeek = () => {
  const days = getDays(7);

  days.push(
    ...new Array(3)
      .fill(1)
      .map(
        (_, index) =>
          new Date(new Date().getTime() + 24 * 60 * 60 * 1000 * (index + 1))
      )
  );

  return getDaysOfWeek(days);
};

export const getPredictMonth = () => {
  const days = getDays(30);

  days.push(
    ...new Array(10)
      .fill(1)
      .map(
        (_, index) =>
          new Date(new Date().getTime() + 24 * 60 * 60 * 1000 * (index + 1))
      )
  );

  return getDaysOfMonth(days);
};

export const getDaysOfMonth = (days: Date[]) =>
  days.map(
    (date) => `${format(date.getDate())}.${format(date.getMonth() + 1)}`
  );

const format = (n: number) => (n < 10 ? `0${n}` : n);

export const getMonths = (count: number) => {
  let last = getMonth(new Date());

  const res: Date[] = [];
  new Array(count).fill(1).forEach(() => {
    const prev = getMonth(new Date(last.getTime() - 24 * 60 * 60 * 1000));
    res.push(last);
    last = prev;
  });

  return res.reverse();
};

export const getMonth = (date: Date) => new Date(date.getFullYear(), date.getMonth());

export const getMonthsNames = (days: Date[]) =>
  days.map(
    (date) => `${format(date.getMonth() + 1)}.${format(date.getFullYear())}`
  );

export const getPredictYear = () => {
  const days = getMonths(12).reverse();

  let last = getMonth(
    new Date(getMonth(new Date()).getTime() + 24 * 60 * 60 * 1000 * 35)
  );

  new Array(3).fill(1).forEach(() => {
    const next = getMonth(new Date(last.getTime() + 24 * 60 * 60 * 1000 * 35));
    days.unshift(last);
    last = next;
  });

  return getMonthsNames(days.reverse());
};
