import { AddRecord } from '@components/add-record';
import { DataTable } from '@components/data-table';
import { ScreenTitle } from '@components/screen-title';

import { useAction, useReduxSelector } from '@services/redux';
import { OutcomeActions } from '@services/redux/outcome';

import { useOutcomeStyles } from './outcome.styles';

export const Outcome = () => {
  const { outcomes } = useReduxSelector((redux) => redux.outcome);
  const handleDelete = useAction(OutcomeActions.delete);
  const handleEdit = useAction(OutcomeActions.update);
  const classes = useOutcomeStyles();

  return (
    <div className={classes.screen}>
      <ScreenTitle title="Outcome" />
      <AddRecord actionAdd={OutcomeActions.add} isIncome={false} />
      <DataTable
        data={outcomes}
        handleDelete={handleDelete}
        handleEdit={handleEdit}
        isIncome={false}
      />
    </div>
  );
};
