import {
  Button, Menu, MenuItem, Paper, Table, TableBody, TableCell, TableContainer,
  TableHead, TableRow, TableSortLabel
} from '@material-ui/core';
import { MoreHoriz } from '@material-ui/icons';
import { FC, MouseEvent } from 'react';

import { DialogPrompt, DialogUser } from '@components/dialog-modal';
import { ScreenTitle } from '@components/screen-title';

import { getAdminCompire } from '@components/data-table/data-table.utils';
import { useAdminState } from './admin.state';

import { useDataTableStyles } from '@components/data-table/data-table.styles';

const headCells = [
  { id: 0, label: 'Email', skipPadding: true, numeric: false },
  { id: 1, label: 'Name', numeric: false },
];

export const Admin = () => {
  const {
    menuAnchorEl,
    isDeleteModal,
    users,
    classes,
    isOpenLogout,
    orderBy,
    order,
    getHandleHeaderClick,
    handleLogout,
    handleOpenDelete,
    handleSubmitDelete,
    handleOpenLogout,
    handleCancelLogout,
    getHandleOpenMenu,
    handleCloseMenu,
    handleCancelDelete,
    isUser,
    handleCloseUser,
    handleOpenUser,
  } = useAdminState();

  return (
    <div className={classes.admin}>
      <ScreenTitle title="Admin" />
      <Paper className={classes.adminPaper}>
        <TableContainer>
          <Table
            className={classes.table}
            aria-labelledby="tableTitle"
            size="medium"
            aria-label="enhanced table"
          >
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox" />
                {headCells.map((headCell) => (
                  <TableCell
                    key={headCell.id}
                    align={headCell.numeric ? 'right' : 'left'}
                    padding={headCell.skipPadding ? 'none' : 'default'}
                    onClick={getHandleHeaderClick(headCell.id)}
                  >
                    <TableSortLabel
                      active={orderBy === headCell.id}
                      direction={order}
                    >
                      {headCell.label}
                    </TableSortLabel>
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {users.sort(getAdminCompire(order, orderBy)).map((row) => (
                <Row
                  id={row.id}
                  name={row.name}
                  email={row.email}
                  getHandleOpenMenu={getHandleOpenMenu}
                  key={row.id}
                />
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <Menu
          id="data-table-menu"
          anchorEl={menuAnchorEl}
          keepMounted
          open={Boolean(menuAnchorEl)}
          onClose={handleCloseMenu}
        >
          <MenuItem onClick={handleOpenDelete}>Delete</MenuItem>
        </Menu>
        <DialogPrompt
          isOpen={isDeleteModal}
          description="This action will delete this user"
          title="Delete"
          handleClose={handleCancelDelete}
          handleSubmit={handleSubmitDelete}
        />
        <DialogPrompt
          isOpen={isOpenLogout}
          description="Are you sure you want to log out?"
          title="Logout"
          handleClose={handleCancelLogout}
          handleSubmit={handleLogout}
        />
        <DialogUser isOpen={isUser} handleClose={handleCloseUser} />
      </Paper>

      <div className={classes.buttons}>
        <Button
          variant="contained"
          color="secondary"
          onClick={handleOpenLogout}
        >
          Logout
        </Button>
        <Button variant="contained" color="primary" onClick={handleOpenUser}>
          Add
        </Button>
      </div>
    </div>
  );
};

interface IRow {
  id: string;
  getHandleOpenMenu: (
    id: string
  ) => (event: MouseEvent<HTMLButtonElement>) => void;
  email: string;
  name: string;
}

const Row: FC<IRow> = ({ getHandleOpenMenu, id, email, name }) => {
  const classes = useDataTableStyles();

  return (
    <TableRow hover tabIndex={-1} role="checkbox">
      <TableCell padding="checkbox">
        <Button
          aria-controls="data-table-menu"
          aria-haspopup="true"
          className={classes.button}
          onClick={getHandleOpenMenu(id)}
        >
          <MoreHoriz className={classes.buttonContent} />
        </Button>
      </TableCell>
      <TableCell component="th" scope="row" padding="none">
        {email}
      </TableCell>
      <TableCell align="left">{name}</TableCell>
    </TableRow>
  );
};
