import { Button, Paper, TextField, Typography } from '@material-ui/core';
import axios from 'axios';
import { ChangeEvent, FC, useState } from 'react';
import { trackPromise } from 'react-promise-tracker';
import { useLocation } from 'react-router';

import { Loader } from '@components/loader';
import { ScreenTitle } from '@components/screen-title';

import { useAction, useReduxSelector } from '@services/redux';
import { AdminActions } from '@services/redux/admin';
import { UserActions } from '@services/redux/user';
import { getUserInfo } from '@services/user.api';

import { CONFIG } from '@constants/config';
import { PROMISES } from '@constants/promises';
import { ROUTES } from '@constants/routes';

import { useLoginStyles } from './login.styles';

export const Login: FC = ({ children }) => {
  const classes = useLoginStyles();

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const { user, admin } = useReduxSelector((redux) => redux);

  const login = useAction(UserActions.login);
  const adminLogin = useAction(AdminActions.login);

  const { pathname } = useLocation();

  const fetchData = () =>
    axios({
      method: 'POST',
      url: `${CONFIG.API}/auth/signin`,
      data: {
        email,
        password,
      },
    })
      .then(async (responce) => {
        const { jwt } = responce.data;
        login(await getUserInfo(jwt));
      })
      .catch((error) => {
        setError(error.response.data.message);
      });

  const fetchAdminData = () =>
    axios({
      method: 'POST',
      url: `${CONFIG.API}/auth/admin`,
      data: {
        email,
        password,
      },
    })
      .then(async (responce) => {
        const { jwt } = responce.data;
        adminLogin({ jwt });
      })
      .catch((error) => setError(error.response.data.message));

  const handleLogin = () => {
    trackPromise(fetchData(), PROMISES.LOGIN);
  };

  const handleAdminLogin = () => {
    trackPromise(fetchAdminData(), PROMISES.LOGIN);
  };

  const handleFocus = () => {
    setError('');
  };

  const getHandleChange = (field: 'email' | 'password') => (
    event: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>
  ) => {
    const v = event.target.value;
    switch (field) {
      case 'email':
        return setEmail(v);
      case 'password':
        return setPassword(v);
    }
  };

  if (user.user || admin.admin) {
    return <>{children}</>;
  }

  return (
    <div className={classes.screen}>
      <Paper className={classes.content} elevation={5}>
        <Loader id={PROMISES.LOGIN} width={400} height={261}>
          <div>
            <ScreenTitle title="Login" />
            <Typography color="error" variant="body1" component="p">
              {error}
            </Typography>
            <TextField
              className={classes.input}
              fullWidth
              id="email"
              label="Email"
              onFocus={handleFocus}
              value={email}
              onChange={getHandleChange('email')}
            />
            <TextField
              className={classes.input}
              fullWidth
              id="password"
              type="password"
              label="Password"
              onFocus={handleFocus}
              value={password}
              onChange={getHandleChange('password')}
            />
            <Button
              variant="contained"
              color="primary"
              onClick={
                pathname === ROUTES.admin ? handleAdminLogin : handleLogin
              }
              className={classes.button}
              onFocus={handleFocus}
            >
              Login
            </Button>
          </div>
        </Loader>
      </Paper>
    </div>
  );
};
