import { combineReducers } from 'redux';

import { TAdminActions } from './admin';
import { TIncomeActions } from './income';
import { TOutcomeActions } from './outcome';
import { TSettingActions } from './setting';
import { TUserActions } from './user';
import { TVidgetActions } from './vidget';

import { reducerAdmin } from './admin/admin.reducer';
import { reducerIncome } from './income/income.reducer';
import { reducerOutcome } from './outcome/outcome.reducer';
import { reducerSetting } from './setting/setting.reducer';
import { reducerUser } from './user/user.reducer';
import { reducerVidget } from './vidget/vidget.reducer';

import { IAdminRedux } from './admin/admin.constants';
import { IIncomeRedux } from './income/income.constants';
import { IOutcomeRedux } from './outcome/outcome.constants';
import { ISettingRedux } from './setting/setting.constants';
import { IUserRedux } from './user/user.constants';
import { IVidgetRedux } from './vidget/vidget.constants';

export interface IReduxStore {
  income: IIncomeRedux;
  outcome: IOutcomeRedux;
  vidget: IVidgetRedux;
  user: IUserRedux;
  setting: ISettingRedux;
  admin: IAdminRedux;
}

export type TActions =
  | TIncomeActions
  | TOutcomeActions
  | TVidgetActions
  | TUserActions
  | TSettingActions
  | TAdminActions;

export const rootReducer = combineReducers({
  income: reducerIncome,
  outcome: reducerOutcome,
  vidget: reducerVidget,
  user: reducerUser,
  setting: reducerSetting,
  admin: reducerAdmin,
}) as any;
