import { Button, Paper, Typography } from '@material-ui/core';

import { DialogModal } from '@components/dialog-modal';
import { PlusButton } from '@components/plus-button';
import { Select } from '@components/select';

import { useNewVidgetState } from './new-vidget.state';
import { getList } from './new-vidget.utils';

import {
  VIDGET_ADDITION_PARAMETERS, VIDGET_MAIN_PARAMETERS, VIDGET_PERIOD
} from './new-vidget.constants';

import { useNewVidgetStyles } from './new-vidget.styles';

export const NewVidget = () => {
  const classes = useNewVidgetStyles();

  const {
    main,
    additional,
    period,
    setMain,
    setAdditional,
    setPeriod,

    isOpenModal,
    handleOpenModal,
    handleCloseModal,

    handleCreate,
  } = useNewVidgetState();

  return (
    <>
      <PlusButton onClick={handleOpenModal} />
      <DialogModal isOpen={isOpenModal} handleClose={handleCloseModal}>
        <Paper className={classes.paper}>
          <Typography gutterBottom variant="h4" component="h2">
            NEW VIDGET
          </Typography>
          <div className={classes.row}>
            <Typography className={classes.label} variant="body1" component="p">
              Base parameter
            </Typography>
            <Select
              list={getList(VIDGET_MAIN_PARAMETERS, additional)}
              value={main}
              setValue={setMain}
            />
          </div>
          <div className={classes.row}>
            <Typography className={classes.label} variant="body1" component="p">
              Additional parameter
            </Typography>
            <Select
              list={getList(VIDGET_ADDITION_PARAMETERS, main)}
              value={additional}
              setValue={setAdditional}
            />
          </div>
          <div className={classes.row}>
            <Typography className={classes.label} variant="body1" component="p">
              Period
            </Typography>
            <Select
              list={getList(VIDGET_PERIOD)}
              value={period}
              setValue={setPeriod}
            />
          </div>
          <Button
            className={classes.button}
            variant="contained"
            color="primary"
            onClick={handleCreate}
          >
            Create
          </Button>
        </Paper>
      </DialogModal>
    </>
  );
};
