import { Grid } from '@material-ui/core';

import { DashboardCard } from '../dashboard-card';
import { NewVidget } from '../new-vidget';

import { usePrice } from '@services/currency.api';
import { useReduxSelector } from '@services/redux';

import { useDashboardVidgetListStyles } from './dashboard-vidget-list.styles';

export const DashboardVidgetList = () => {
  const classes = useDashboardVidgetListStyles();

  const { vidgets } = useReduxSelector((redux) => redux.vidget);
  const convert = usePrice();

  return (
    <Grid
      className={classes.container}
      container
      justify="flex-start"
      spacing={3}
    >
      {vidgets.map((vidget) => (
        <Grid item key={vidget.id}>
          <DashboardCard vidget={vidget} convert={convert} />
        </Grid>
      ))}
      <Grid item>
        <NewVidget />
      </Grid>
    </Grid>
  );
};
