import {
  Button, Card, CardContent, TextField, Typography
} from '@material-ui/core';
import axios from 'axios';
import { FC, useState } from 'react';
import { trackPromise } from 'react-promise-tracker';

import { Loader } from '@components/loader';
import { DialogModal, IDialogModal } from '../dialog-modal';

import { useAction, useReduxSelector } from '@services/redux';
import { AdminActions } from '@services/redux/admin';
import { useDialogRecordState } from './dialog-user.state';

import { CONFIG } from '@constants/config';
import { PROMISES } from '@constants/promises';

import { useDialogStyles } from '../dialog.styles';

export const DialogUser: FC<IDialogModal> = ({ isOpen, handleClose }) => {
  const classes = useDialogStyles();
  const [name, setName] = useState('');
  const [location, setLocation] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const { getHandleChangeText } = useDialogRecordState();

  const [error, setError] = useState('');
  const handleFocus = () => setError('');

  const { admin } = useReduxSelector((redux) => redux.admin);

  const add = useAction(AdminActions.add);

  const signup = () =>
    axios({
      method: 'POST',
      url: `${CONFIG.API}/admin/signup`,
      data: {
        name,
        location,
        email,
        password,
        currency: 'USD',
        language: 0,
      },
      headers: {
        Authorization: admin?.jwt || '',
      },
    })
      .then((response) => response.data.data)
      .catch((error) => setError(error.response.data.message));

  const handleSave = async () => {
    const id = await trackPromise(signup(), PROMISES.DIALOG_USER);
    if (!id) {
      return;
    }
    add({
      name,
      email,
      id,
    });
    handleClose();
  };

  return (
    <DialogModal isOpen={isOpen} handleClose={handleClose}>
      <Card className={classes.record}>
        <CardContent>
          <Typography gutterBottom variant="h4" component="h2">
            New user
          </Typography>
          <Typography
            className={classes.textInput}
            color="error"
            variant="body1"
            component="p"
          >
            {error}
          </Typography>
          <TextField
            label="Name"
            className={classes.textInput}
            value={name}
            onChange={getHandleChangeText(setName, 24)}
            onFocus={handleFocus}
          />
          <TextField
            label="Email"
            className={classes.textInput}
            value={email}
            onChange={getHandleChangeText(setEmail, 60)}
            onFocus={handleFocus}
          />
          <TextField
            label="Location"
            className={classes.textInput}
            value={location}
            onChange={getHandleChangeText(setLocation, 32)}
            onFocus={handleFocus}
          />
          <TextField
            label="Password"
            type="password"
            className={classes.textInput}
            value={password}
            onChange={getHandleChangeText(setPassword, 32)}
            onFocus={handleFocus}
          />
          <Loader id={PROMISES.DIALOG_USER}>
            <Button
              variant="contained"
              color="primary"
              onClick={handleSave}
              className={classes.button}
            >
              Save
            </Button>
          </Loader>
        </CardContent>
      </Card>
    </DialogModal>
  );
};
