import { useState } from 'react';

import { useAction } from '@services/redux';
import { VidgetActions } from '@services/redux/vidget';
import { getUUID } from '@services/utils';

import {
  VIDGET_ADDITION_PARAMETERS, VIDGET_MAIN_PARAMETERS, VIDGET_PERIOD
} from './new-vidget.constants';

export const useNewVidgetState = () => {
  const [main, setMain] = useState(VIDGET_MAIN_PARAMETERS[0]);
  const [additional, setAdditional] = useState(VIDGET_ADDITION_PARAMETERS[0]);
  const [period, setPeriod] = useState(VIDGET_PERIOD[0]);

  const [isOpenModal, setOpenModal] = useState(false);

  const add = useAction(VidgetActions.add);

  const handleOpenModal = () => setOpenModal(true);
  const handleCloseModal = () => {
    setOpenModal(false);
    setMain(VIDGET_MAIN_PARAMETERS[0]);
    setAdditional(VIDGET_ADDITION_PARAMETERS[0]);
    setPeriod(VIDGET_PERIOD[0]);
  }

  const handleCreate = () => {
    add({
      id: getUUID(),
      base: main,
      additional,
      period,
    });
    handleCloseModal();
  }

  return {
    main,
    additional,
    period,
    setMain,
    setAdditional,
    setPeriod,

    isOpenModal,
    handleOpenModal,
    handleCloseModal,

    handleCreate,
  };
};
