import { Redirect, Route, Switch } from 'react-router-dom';

import { Navigation } from '@components/navigation';
import { Admin } from '@screens/admin';
import { Dashboard } from '@screens/dashboard';
import { Income } from '@screens/income';
import { Outcome } from '@screens/outcome';
import { Profile } from '@screens/profile';

import { useReduxSelector } from '@services/redux';

import { ROUTES } from '@constants/routes';

export const AppRouter = () => {
  const { admin } = useReduxSelector((redux) => redux.admin);

  if (admin) {
    return <Admin />;
  }

  return (
    <div className="app">
      <Navigation />
      <main style={{ width: '100%' }}>
        <Switch>
          <Route exact path={ROUTES.dashboard} component={Dashboard} />

          <Route path={`${ROUTES.income}/:id`} component={Income} />
          <Redirect path={ROUTES.income} to={`${ROUTES.income}/1`} />

          <Route path={`${ROUTES.outcome}/:id`} component={Outcome} />
          <Redirect path={ROUTES.outcome} to={`${ROUTES.outcome}/1`} />

          <Route path={ROUTES.profile} exact component={Profile} />
        </Switch>
      </main>
    </div>
  );
};
