import { TIncomeActions } from './income.actions';

import { IIncomeRedux, INCOME_ACTIONS, INIT_INCOME } from './income.constants';

const { ADD, UPDATE, DELETE, SET } = INCOME_ACTIONS;

export const reducerIncome = (
  state = INIT_INCOME,
  action: TIncomeActions
): IIncomeRedux => {
  switch (action.type) {
    case ADD:
      return {
        incomes: [...state.incomes, action.payload],
      };

    case UPDATE:
      return {
        incomes: state.incomes.map((el) => {
          if (el.id === action.payload.id) {
            return action.payload;
          }
          return el;
        }),
      };

    case DELETE:
      return {
        incomes: state.incomes.filter(({ id }) => id !== action.payload),
      };

    case SET:
      return {
        incomes: [...action.payload],
      };

    default:
      return state ?? INIT_INCOME;
  }
};
