import { FC, useEffect, useState } from 'react';
import { trackPromise } from 'react-promise-tracker';

import { DialogRecord } from '@components/dialog-modal';

import { editRecord } from '@services/records.api';
import { useReduxSelector } from '@services/redux';
import { getCurrency, getUUID } from '@services/utils';

import { Currency } from '@constants/currency';
import { PROMISES } from '@constants/promises';

interface IDataTableEdit {
  record?: IRecord;
  handleEdit: (record: IRecord) => void;
  handleCloseModal: () => void;
  isEditModal: boolean;
  isIncome: boolean;
}

export const DataTableEdit: FC<IDataTableEdit> = ({
  record,
  handleEdit,
  isEditModal,
  handleCloseModal,
  isIncome,
}) => {
  const [title, setTitle] = useState(record?.title || '');
  const [description, setDescription] = useState(record?.description || '');
  const [value, setValue] = useState(record?.value.toString() || '');
  const [currency, setCurrency] = useState(record?.currency || Currency.USD);
  const [date, setDate] = useState(record?.currency || Currency.USD);
  const [error, setError] = useState('');
  const handleFocus = () => setError('');

  const handleDateChange = (date: Date | null) => setDate(date);

  const { user } = useReduxSelector((redux) => redux.user);

  useEffect(() => {
    setTitle(record?.title || '');
    setDescription(record?.description || '');
    setValue(record?.value.toString() || '');
    setCurrency(record?.currency || Currency.USD);
    setDate(new Date(record?.time || Date.now()));
  }, [record]);

  const handleSave = async () => {
    await trackPromise(
      editRecord(user?.jwt || '', {
        title,
        description,
        value: parseFloat(value),
        currency: getCurrency(currency),
        time: date.getTime(),
        isIncome,
        id: record?.id,
      }),
      PROMISES.DIALOG_RECORD
    );
    handleEdit({
      title,
      description,
      value: parseFloat(value),
      currency,
      time: date.getTime(),
      id: record?.id || getUUID(),
    });
    handleCloseModal();
  };

  if (!record) {
    return null;
  }

  return (
    <DialogRecord
      isOpen={isEditModal}
      title={title}
      description={description}
      value={value}
      setTitle={setTitle}
      setDescription={setDescription}
      setValue={setValue}
      handleClose={handleCloseModal}
      currency={currency}
      setCurrency={setCurrency}
      handleSave={handleSave}
      date={date}
      handleDateChange={handleDateChange}
      error={error}
      handleFocus={handleFocus}
    />
  );
};
