import axios from 'axios';

import { CONFIG } from '@constants/config';

interface IAPIRecors extends IRecord {
  isIncome: boolean;
}

export const getRecords = async (jwt: string): Promise<IAPIRecors[]> => {
  const { data } = (
    await axios({
      method: 'GET',
      url: `${CONFIG.API}/record`,
      headers: {
        Authorization: jwt,
      },
    })
  ).data;

  return data;
};

export const addRecord = async (
  jwt: string,
  record: Partial<IRecord | { isIncome: boolean }>
): Promise<string | { id: string }> => {
  const { message, data } = (
    await axios({
      method: 'POST',
      url: `${CONFIG.API}/record`,
      headers: {
        Authorization: jwt,
      },
      data: record,
    })
  ).data;

  if (message) {
    return message;
  }

  return data;
};

export const editRecord = async (
  jwt: string,
  record: Partial<IRecord | { isIncome: boolean }>
): Promise<string | undefined> => {
  const { message } = (
    await axios({
      method: 'PUT',
      url: `${CONFIG.API}/record`,
      headers: {
        Authorization: jwt,
      },
      data: record,
    })
  ).data;

  if (message) {
    return message;
  }
};

export const removeRecord = async (
  jwt: string,
  recordId: string
): Promise<string | undefined> => {
  const { message } = (
    await axios({
      method: 'DELETE',
      url: `${CONFIG.API}/record/${recordId}`,
      headers: {
        Authorization: jwt,
      },
    })
  ).data;

  if (message) {
    return message;
  }
};
