import { VIDGET_PERIOD } from '@screens/dashboard/components';

export const getPrediction = (data: number[], period: string) => {
  const n = data.length;

  const x = getXSum(data, 1);
  const x2 = getXSum(data, 2);

  const y = getYSum(data, 0);
  const xy = getYSum(data, 1);

  const matrix = [
    [n, x],
    [x, x2],
  ];
  const results = [y, xy];

  const { det, detX1, detX2 } = determinants(matrix, results);

  if (det !== 0) {
    const a = detX1 / det;
    const b = detX2 / det;

    switch (period) {
      case VIDGET_PERIOD[2]:
        return new Array(15).fill(1).map((_, i) => +((i - a) / b).toFixed(2));
      case VIDGET_PERIOD[1]:
        return new Array(40).fill(1).map((_, i) => +((i - a) / b).toFixed(2));
      default:
        return new Array(10).fill(1).map((_, i) => +((i - a) / b).toFixed(2));
    }
  } else {
    return [];
  }
};

const getXSum = (data: number[], pow: number) =>
  data.reduce((acc, val) => acc + Math.pow(val, pow), 0);

const getYSum = (data: number[], pow: number) =>
  data.map((x, y) => y * Math.pow(x, pow)).reduce((acc, val) => acc + val, 0);

const determinants = (matrix: number[][], results: number[]) => {
  const a11 = matrix[0][0];
  const a12 = matrix[0][1];
  const a21 = matrix[1][0];
  const a22 = matrix[1][1];
  const c1 = results[0];
  const c2 = results[1];

  const det = a11 * a22 - a12 * a21;

  const detX1 = c1 * a22 - a12 * c2;

  const detX2 = a11 * c2 - c1 * a21;

  return { det, detX1, detX2 };
};
