import { action, TActionCombiner } from '../utils';

import { SETTING_ACTIONS } from './setting.constants';

const { GET } = SETTING_ACTIONS;

export const SettingActions = {
  get: (settings: ISetting[]) => action(GET, settings),
};

export type TSettingActions = ReturnType<
  TActionCombiner<typeof SettingActions>
>;
