import { TOutcomeActions } from './outcome.actions';

import {
  INIT_OUTCOME, IOutcomeRedux, OUTCOME_ACTIONS
} from './outcome.constants';

const { ADD, UPDATE, DELETE, SET } = OUTCOME_ACTIONS;

export const reducerOutcome = (
  state = INIT_OUTCOME,
  action: TOutcomeActions
): IOutcomeRedux => {
  switch (action.type) {
    case ADD:
      return {
        outcomes: [...state.outcomes, action.payload],
      };

    case UPDATE:
      return {
        outcomes: state.outcomes.map((el) => {
          if (el.id === action.payload.id) {
            return action.payload;
          }
          return el;
        }),
      };

    case DELETE:
      return {
        outcomes: state.outcomes.filter(({ id }) => id !== action.payload),
      };

    case SET:
      return {
        outcomes: [...action.payload],
      };

    default:
      return state ?? INIT_OUTCOME;
  }
};
