import { FormControl, MenuItem, Select as MSelect } from '@material-ui/core';
import { ChangeEvent, FC } from 'react';

import { useSelectStyles } from './select.styles';

interface ISelect {
  list: { value: any; label: string }[];
  value: any;
  setValue: (value: any) => void;
}

export const Select: FC<ISelect> = ({ list, value, setValue }) => {
  const handleChange = (event: ChangeEvent<{ value: any }>) => {
    setValue(event.target.value);
  };

  const classes = useSelectStyles();

  return (
    <FormControl className={classes.selectContainer}>
      <MSelect
        value={value}
        onChange={handleChange}
        displayEmpty
        className={classes.select}
      >
        {list.map((el) => (
          <MenuItem key={el.label} value={el.value}>
            {el.label}
          </MenuItem>
        ))}
      </MSelect>
    </FormControl>
  );
};
