import { Button, TextField, Typography } from '@material-ui/core';
import { ChangeEvent, useEffect, useState } from 'react';
import { trackPromise } from 'react-promise-tracker';

import { DialogPrompt } from '@components/dialog-modal';
import { Loader } from '@components/loader';
import { ScreenTitle } from '@components/screen-title';
import { Select } from '@components/select';

import { useAction, useReduxSelector } from '@services/redux';
import { UserActions } from '@services/redux/user';
import { updateInfo } from '@services/user.api';
import { getCurrency, getLaunguage, parseCurrency } from '@services/utils';

import { CurrencyList } from '@constants/currency';
import { Language } from '@constants/language';
// import { LanguageList } from '@constants/language';
import { PROMISES } from '@constants/promises';

import { useProfileStyles } from './profile.styles';

export const Profile = () => {
  const classes = useProfileStyles();
  const { user } = useReduxSelector((redux) => redux.user);

  const logout = useAction(UserActions.logout);
  const login = useAction(UserActions.login);

  const [name, setName] = useState(user?.name ?? '');
  const [email, setEmail] = useState(user?.email ?? '');
  const [location, setLocation] = useState(user?.location ?? '');
  const [currency, setCurrency] = useState(parseCurrency(user?.currency));
  // const [language, setLanguage] = useState(parseLanguage(user?.language));

  const [error, setError] = useState('');

  const handleFocus = () => setError('');

  const [isOpenLogout, setOpenLogout] = useState(false);

  useEffect(() => {
    setEmployee(user?.employee?.toString() || '');
  }, [user?.employee]);

  const [employee, setEmployee] = useState(user?.employee?.toString() || '');

  const getHandleChange = (setter: (value: string) => void) => (
    event: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>
  ) => {
    const v = event.target.value;

    setter(v);
  };

  const handleLogout = () => {
    logout();
  };

  const handleCancelLogout = () => {
    setOpenLogout(false);
  };

  const handleOpenLogout = () => {
    setOpenLogout(true);
  };

  const handleChangeEmployee = (
    event: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>
  ) => {
    const v = event.target.value;
    if (v === '') {
      return setEmployee(v);
    }
    const value = parseInt(v);
    if (value < 0) {
      return setEmployee('0');
    }
    setEmployee(value.toString(10));
  };

  const handleUpdate = async () => {
    const u = {
      jwt: user?.jwt || '',
      currency: getCurrency(currency),
      email,
      name,
      location,
      language: getLaunguage(Language.EN),
      employee: +employee,
    };

    const e = await trackPromise(updateInfo(u, login), PROMISES.UPDATE);
    setError(e);

    if (!e && u.email !== user?.email) {
      logout();
      alert('You logged out because you change email');
    }
  };

  return (
    <div className={classes.screen}>
      <ScreenTitle title="Profile" />
      <Typography
        className={classes.input}
        color="error"
        variant="body1"
        component="p"
      >
        {error}
      </Typography>
      <TextField
        className={classes.input}
        fullWidth
        id="name"
        label="Name"
        value={name}
        onChange={getHandleChange(setName)}
        onFocus={handleFocus}
      />
      <TextField
        className={classes.input}
        fullWidth
        id="email"
        label="Email"
        value={email}
        onChange={getHandleChange(setEmail)}
        onFocus={handleFocus}
      />
      <TextField
        className={classes.input}
        fullWidth
        id="location"
        label="Location"
        value={location}
        onChange={getHandleChange(setLocation)}
        onFocus={handleFocus}
      />
      <TextField
        className={classes.input}
        fullWidth
        type="number"
        label="Employee"
        value={employee}
        onChange={handleChangeEmployee}
        onFocus={handleFocus}
      />
      <div className={classes.currency}>
        <Typography>Default Currency</Typography>
        <div className={classes.currencySelect}>
          <Select list={CurrencyList} value={currency} setValue={setCurrency} />
        </div>
      </div>
      {/* <div className={classes.currency}>
        <Typography>Language</Typography>
        <div className={classes.currencySelect}>
          <Select list={LanguageList} value={language} setValue={setLanguage} />
        </div>
      </div> */}

      <DialogPrompt
        isOpen={isOpenLogout}
        description="Are you sure you want to log out?"
        title="Logout"
        handleClose={handleCancelLogout}
        handleSubmit={handleLogout}
      />

      <Loader id={PROMISES.UPDATE} width={400} height={36}>
        <div className={classes.buttons}>
          <Button
            variant="contained"
            color="secondary"
            onClick={handleOpenLogout}
          >
            Logout
          </Button>
          <Button variant="contained" color="primary" onClick={handleUpdate}>
            Save
          </Button>
        </div>
      </Loader>
    </div>
  );
};
