import axios from 'axios';

import { CONFIG } from '@constants/config';

import { getUUID } from './utils';

export const checkJWT = (jwt: string) =>
  new Promise((resolve) => {
    axios({
      method: 'GET',
      url: `${CONFIG.API}/user/profile`,
      headers: {
        Authorization: jwt,
      },
    })
      .then((res) => {
        if (res.data.data) {
          resolve(true);
        } else {
          resolve(false);
        }
      })
      .catch(() => resolve(false));
  });

export const getUserInfo = async (jwt: string): Promise<IUser> => {
  const { data } = (
    await axios({
      method: 'GET',
      url: `${CONFIG.API}/user/profile`,
      headers: {
        Authorization: jwt,
      },
    })
  ).data;

  const empl = (
    await axios({
      method: 'GET',
      url: `${CONFIG.API}/settings/last`,
      headers: {
        Authorization: jwt,
      },
    })
  ).data;

  return {
    jwt,
    currency: data.currency,
    email: data.email,
    language: data.language,
    location: data.location,
    name: data.name,
    employee: empl?.data?.employee ?? 0,
  };
};

export const updateInfo = async (user: IUser, login: (user: IUser) => void) => {
  try {
    const { data, message, ok } = (
      await axios({
        method: 'PUT',
        url: `${CONFIG.API}/user/profile`,
        headers: {
          Authorization: user.jwt,
        },
        data: user,
      })
    ).data;

    const settings = (
      await axios({
        method: 'POST',
        url: `${CONFIG.API}/settings`,
        headers: {
          Authorization: user.jwt,
        },
        data: {
          employee: user.employee,
          timestamp: new Date().getTime(),
        },
      })
    ).data;

    if (!ok || !settings.ok || !data) {
      throw new Error(message || '');
    }

    login({
      jwt: user.jwt,
      employee: user.employee,
      currency: data.currency,
      email: data.email,
      language: data.language,
      location: data.location,
      name: data.name,
    });
  } catch (error) {
    return error.message;
  }
};

export const getSettings = async (jwt: string): Promise<ISetting[]> => {
  const { data }: { data: any[] } = (
    await axios({
      method: 'GET',
      url: `${CONFIG.API}/settings`,
      headers: {
        Authorization: jwt,
      },
    })
  ).data;

  return data.map((el) => ({
    employee: el.employee,
    timestamp: el.timestamp,
    id: getUUID(),
  }));
};
