import { useState } from 'react';
import { trackPromise } from 'react-promise-tracker';

import { addRecord } from '@services/records.api';
import { useAction, useReduxSelector } from '@services/redux';
import { getCurrency } from '@services/utils';

import { Currency } from '@constants/currency';
import { PROMISES } from '@constants/promises';

export const useAddRecordState = (actionAdd: any, isIncome: boolean) => {
  const [isModalOpen, setModalOpen] = useState(false);
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [value, setValue] = useState('');
  const [currency, setCurrency] = useState(Currency.USD);
  const [error, setError] = useState('');
  const add = useAction(actionAdd);

  const [date, setDate] = useState<Date | null>(new Date());
  const handleDateChange = (date: Date | null) => {
    setDate(date);
  };

  const { user } = useReduxSelector((redux) => redux.user);

  const handleOpenModal = () => {
    setModalOpen(true);
  };

  const handleFocus = () => setError('');

  const handleCloseModal = () => {
    setTitle('');
    setDescription('');
    setValue('');
    setCurrency(Currency.USD);
    setModalOpen(false);
  };

  const handleSave = async () => {
    const error = await trackPromise(
      addRecord(user?.jwt || '', {
        title,
        description,
        value: parseFloat(value),
        currency: getCurrency(currency),
        time: date ? date.getTime() : new Date().getTime(),
        isIncome,
      }),
      PROMISES.DIALOG_RECORD
    );
    if (typeof error === 'string') {
      setError(error);
    } else {
      add({
        id: error.id,
        time: date ? date.getTime() : new Date().getTime(),
        title,
        description,
        currency,
        value: parseFloat(value),
      });
      handleCloseModal();
    }
  };

  return {
    isModalOpen,
    title,
    description,
    value,
    setTitle,
    setDescription,
    setValue,
    currency,
    setCurrency,

    date,
    handleDateChange,

    handleOpenModal,
    handleCloseModal,
    handleSave,

    error,
    handleFocus,
  };
};
