import { action, TActionCombiner } from '../utils';

import { VIDGET_ACTIONS } from './vidget.constants';

const { ADD, DELETE } = VIDGET_ACTIONS;

export const VidgetActions = {
  add: (vidget: IVidget) => action(ADD, vidget),
  delete: (id: string) => action(DELETE, id),
};

export type TVidgetActions = ReturnType<TActionCombiner<typeof VidgetActions>>;
