import { MouseEvent, useState } from 'react';
import { trackPromise } from 'react-promise-tracker';
import { useHistory, useLocation, useParams } from 'react-router';

import { removeRecord } from '@services/records.api';
import { useReduxSelector } from '@services/redux';
import { useQuery } from '@services/utils';

import { Currency } from '@constants/currency';
import { PROMISES } from '@constants/promises';

export enum Order {
  ASC = 'asc',
  DESC = 'desc',
}

interface IUseDataTableState {
  handleDelete: (id: string) => void;
}

export const useDataTableState = ({ handleDelete }: IUseDataTableState) => {
  /* TABLE CONTROLS */
  const { id } = useParams<{ id: string }>();
  const page = parseInt(id) || 1;
  const [orderBy, setOrderBy] = useState(3);
  const [order, setOrder] = useState(Order.DESC);
  const query = useQuery();
  const location = useLocation();
  const history = useHistory();

  const [currency, setCurrency] = useState(Currency.USD);

  const getHandleHeaderClick = (id: number) => () => {
    if (orderBy === id) {
      return setOrder((o) => (o === Order.DESC ? Order.ASC : Order.DESC));
    }
    setOrderBy(id);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    history.replace(`${getPath()}/1?rowsPerPage=${event.target.value}`);
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    history.push(
      `${getPath()}/${newPage + 1}?rowsPerPage=${
        query.get('rowsPerPage') || 10
      }`
    );
  };

  const getPath = () => {
    const pathArr = location.pathname.split('/');
    return pathArr.slice(0, pathArr.length - 1).join('/');
  };

  /* TABLE MENU */
  const [isDeleteModal, setDeleteModal] = useState(false);
  const [isEditModal, setEditModal] = useState(false);
  const [menuAnchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [activeRow, setActiveRow] = useState('');

  const { user } = useReduxSelector((redux) => redux.user);

  const getHandleOpenMenu = (id: string) => (
    event: MouseEvent<HTMLButtonElement>
  ) => {
    setAnchorEl(event.currentTarget);
    setActiveRow(id);
  };

  const handleCloseMenu = () => {
    setAnchorEl(null);
  };

  const handleOpenDelete = () => {
    handleCloseMenu();
    setDeleteModal(true);
  };

  const handleSubmitDelete = async () => {
    const error = await trackPromise(
      removeRecord(user?.jwt || '', activeRow),
      PROMISES.DIALOG_PROMPT
    );
    if (!error) {
      handleDelete(activeRow);
      handleCancelDelete();
    }
  };

  const handleCancelDelete = () => {
    setDeleteModal(false);
  };

  const handleOpenEdit = () => {
    handleCloseMenu();
    setEditModal(true);
  };

  const handleCloseEdit = () => {
    setEditModal(false);
  };

  const getActiveRecord = (data: IRecord[]) =>
    data.find(({ id }) => id === activeRow);

  return {
    page,
    orderBy,
    order,
    rowsPerPage: parseInt(query.get('rowsPerPage') || '') || 10,
    getHandleHeaderClick,
    handleChangeRowsPerPage,
    handleChangePage,

    menuAnchorEl,
    getHandleOpenMenu,
    handleCloseMenu,

    handleOpenDelete,
    handleSubmitDelete,
    handleCancelDelete,
    isDeleteModal,

    handleOpenEdit,
    handleCloseEdit,
    isEditModal,

    getActiveRecord,

    currency,
    setCurrency,
  };
};
