import { CircularProgress } from '@material-ui/core';
import { FC } from 'react';
import { usePromiseTracker } from 'react-promise-tracker';

interface ILoader {
  id: string;
  height?: number;
  width?: number;
  justify?: string;
}

export const Loader: FC<ILoader> = ({
  children,
  id,
  height,
  width,
  justify,
}) => {
  const { promiseInProgress } = usePromiseTracker({ area: id });

  return (
    <div
      style={{
        display: 'flex',
        justifyContent: justify ?? 'center',
        alignItems: 'center',
        height: height ?? 30,
        width,
      }}
    >
      {promiseInProgress ? <CircularProgress /> : children}
    </div>
  );
};
