import { MouseEvent, useState } from 'react';
import { trackPromise } from 'react-promise-tracker';

import { Order } from '@components/data-table/data-table.state';
import { deleteUser } from '@services/admin.api';
import { useAction, useReduxSelector } from '@services/redux';
import { AdminActions } from '@services/redux/admin';
import { useAdmin } from '@services/utils';

import { PROMISES } from '@constants/promises';

import { useDataTableStyles } from '@components/data-table/data-table.styles';

export const useAdminState = () => {
  const [menuAnchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [isDeleteModal, setDeleteModal] = useState(false);
  const [activeRow, setActiveRow] = useState('');
  const { users, admin } = useReduxSelector((redux) => redux.admin);
  const classes = useDataTableStyles();

  const [isOpenLogout, setOpenLogout] = useState(false);

  const [isUser, setUser] = useState(false);

  const [orderBy, setOrderBy] = useState(0);
  const [order, setOrder] = useState(Order.DESC);

  const getHandleHeaderClick = (id: number) => () => {
    if (orderBy === id) {
      return setOrder((o) => (o === Order.DESC ? Order.ASC : Order.DESC));
    }
    setOrderBy(id);
  };

  useAdmin();

  const handleDelete = useAction(AdminActions.delete);
  const handleLogout = useAction(AdminActions.logout);

  const handleCloseMenu = () => {
    setAnchorEl(null);
  };

  const handleOpenDelete = () => {
    handleCloseMenu();
    setDeleteModal(true);
  };

  const handleCloseUser = () => setUser(false);
  const handleOpenUser = () => setUser(true);

  const handleSubmitDelete = async () => {
    const error = await trackPromise(
      deleteUser(admin?.jwt || '', activeRow),
      PROMISES.DIALOG_PROMPT
    );
    if (!error) {
      handleDelete(activeRow);
      handleCancelDelete();
    }
  };

  const handleCancelDelete = () => {
    setDeleteModal(false);
  };
  const handleOpenLogout = () => {
    setOpenLogout(true);
  };
  const handleCancelLogout = () => {
    setOpenLogout(false);
  };

  const getHandleOpenMenu = (id: string) => (
    event: MouseEvent<HTMLButtonElement>
  ) => {
    setAnchorEl(event.currentTarget);
    setActiveRow(id);
  };

  return {
    menuAnchorEl,
    isDeleteModal,
    users,
    classes,
    isOpenLogout,
    order,
    orderBy,
    getHandleHeaderClick,
    handleLogout,
    handleOpenDelete,
    handleSubmitDelete,
    handleOpenLogout,
    handleCancelLogout,
    getHandleOpenMenu,
    handleCloseMenu,
    handleCancelDelete,
    isUser,
    handleCloseUser,
    handleOpenUser,
  };
};
