import { TAdminActions } from './admin.actions';

import { ADMIN_ACTIONS, IAdminRedux, INIT_ADMIN } from './admin.constants';

const { LOGIN, LOGOUT, USERS, DELETE, ADD } = ADMIN_ACTIONS;

export const reducerAdmin = (
  state = INIT_ADMIN,
  action: TAdminActions
): IAdminRedux => {
  switch (action.type) {
    case LOGIN:
      return {
        ...state,
        admin: action.payload,
      };

    case USERS:
      return {
        ...state,
        users: action.payload,
      };

    case DELETE:
      return {
        ...state,
        users: state.users.filter(({ id }) => id !== action.payload),
      };

    case ADD:
      return {
        ...state,
        users: [...state.users, action.payload],
      };

    case LOGOUT:
      return INIT_ADMIN;

    default:
      return state ?? INIT_ADMIN;
  }
};
