import {
  VIDGET_ADDITION_PARAMETERS, VIDGET_PERIOD
} from '@screens/dashboard/components';
import { getDays, getMonth, getMonths } from './format';

import { getPrice } from '@services/currency.api';
import { IReduxStore } from '@services/redux';
import { parseCurrency } from '@services/utils';

import { Currency } from '@constants/currency';

export const getData = (
  redux: IReduxStore,
  type: string,
  convert: readonly [number, number],
  period: string
) => {
  let dates = getDays(7);

  switch (period) {
    case VIDGET_PERIOD[1]:
      dates = getDays(30);
      break;
    case VIDGET_PERIOD[2]:
      dates = getMonths(12);
      break;
  }

  if (type === VIDGET_ADDITION_PARAMETERS[3]) {
    return getEmployeeData(redux.setting.settings, dates, period);
  }

  const { incomes } = redux.income;
  const { outcomes } = redux.outcome;

  return dates.map((from) => {
    const to =
      period === VIDGET_PERIOD[2]
        ? getMonth(new Date(from.getTime() + 24 * 60 * 60 * 1000 * 35)).getTime()
        : new Date(from.getTime() + 24 * 60 * 60 * 1000).getTime();

    switch (type) {
      case VIDGET_ADDITION_PARAMETERS[2]:
        return formatData(outcomes, from.getTime(), to, convert, parseCurrency(redux.user.user?.currency || 'USD'));
      default:
        return formatData(incomes, from.getTime(), to, convert, parseCurrency(redux.user.user?.currency || 'USD'));
    }
  });
};

export const getEmployeeData = (settings: ISetting[], dates: Date[], period: string) => {
  return dates.map((from) => {
    const to =
      period === VIDGET_PERIOD[2]
        ? getMonth(new Date(from.getTime() + 24 * 60 * 60 * 1000 * 35)).getTime()
        : new Date(from.getTime() + 24 * 60 * 60 * 1000).getTime();

    return (
      settings
        .filter(({ timestamp }) => from.getTime() < timestamp && timestamp < to)
        .sort((a, b) => b.timestamp - a.timestamp)[0]?.employee || 0
    );
  });
};

const formatData = (
  data: IRecord[],
  from: number,
  to: number,
  convert: readonly [number, number],
  currency: Currency,
) =>
  +data
    .filter(({ time }) => from < time && time < to)
    .reduce((acc, val) => {
      return acc + getPrice(val.value, val.currency, currency, convert);
    }, 0)
    .toFixed(2);
