import {
  Button, Card, CardContent, TextField, Typography
} from '@material-ui/core';
import { DatePicker } from '@material-ui/pickers';
import { FC } from 'react';

import { Loader } from '@components/loader';
import { Select } from '@components/select';
import { DialogModal, IDialogModal } from '../dialog-modal';

import { useDialogRecordState } from './dialog-record.state';

import { Currency, CurrencyList } from '@constants/currency';
import { PROMISES } from '@constants/promises';

import { useDialogStyles } from '../dialog.styles';

export interface IDialogRecord extends IDialogModal {
  title: string;
  setTitle: (title: string) => void;
  value: string;
  setValue: (value: string) => void;
  currency: Currency;
  setCurrency: (currency: Currency) => void;
  description: string;
  setDescription: (description: string) => void;
  handleSave: () => void;

  date: Date | null;
  handleDateChange: (date: Date | null) => void;

  handleFocus: () => void;
  error: string;
}

export const DialogRecord: FC<IDialogRecord> = ({
  isOpen,
  title,
  setTitle,
  description,
  setDescription,
  value,
  setValue,
  handleClose,
  currency,
  setCurrency,
  handleSave,
  date,
  handleDateChange,
  handleFocus,
  error,
}) => {
  const classes = useDialogStyles();
  const { getHandleChangeText, handleChangeValue } = useDialogRecordState(
    setValue
  );

  return (
    <DialogModal isOpen={isOpen} handleClose={handleClose}>
      <Card className={classes.record}>
        <CardContent>
          <Typography gutterBottom variant="h4" component="h2">
            {title || 'New record'}
          </Typography>
          <Typography
            className={classes.textInput}
            color="error"
            variant="body1"
            component="p"
          >
            {error}
          </Typography>
          <TextField
            label="Title"
            className={classes.textInput}
            value={title}
            onChange={getHandleChangeText(setTitle, 24)}
            onFocus={handleFocus}
          />
          <div style={{ display: 'flex', flexDirection: 'row' }}>
            <TextField
              type="number"
              label="Value"
              className={classes.textInput}
              value={value}
              onChange={handleChangeValue}
              onFocus={handleFocus}
            />
            <Select
              list={CurrencyList}
              value={currency}
              setValue={setCurrency}
            />
          </div>
          <DatePicker
            fullWidth
            margin="normal"
            id="date-picker-dialog"
            label="Date"
            format="MM/dd/yyyy"
            maxDate={new Date()}
            value={date}
            onChange={handleDateChange}
            onFocus={handleFocus}
          />
          <TextField
            className={classes.textInput}
            label="Description"
            value={description}
            onChange={getHandleChangeText(setDescription)}
            multiline
            rows={4}
            onFocus={handleFocus}
          />
          <Loader id={PROMISES.DIALOG_RECORD}>
            <Button
              variant="contained"
              color="primary"
              onClick={handleSave}
              className={classes.button}
            >
              Save
            </Button>
          </Loader>
        </CardContent>
      </Card>
    </DialogModal>
  );
};
