import { action, TActionCombiner } from '../utils';

import { INCOME_ACTIONS } from './income.constants';

const { ADD, UPDATE, DELETE, SET } = INCOME_ACTIONS;

export const IncomeActions = {
  add: (income: IRecord) => action(ADD, income),
  update: (income: IRecord) => action(UPDATE, income),
  delete: (id: string) => action(DELETE, id),
  set: (incomes: IRecord[]) => action(SET, incomes)
};

export type TIncomeActions = ReturnType<TActionCombiner<typeof IncomeActions>>;
