import { TUserActions } from '../user';

import { TVidgetActions } from './vidget.actions';

import { INIT_VIDGET, IVidgetRedux, VIDGET_ACTIONS } from './vidget.constants';

const { ADD, DELETE } = VIDGET_ACTIONS;

export const reducerVidget = (
  state = INIT_VIDGET,
  action: TVidgetActions | TUserActions
): IVidgetRedux => {
  switch (action.type) {
    case ADD:
      return {
        vidgets: [...state.vidgets, action.payload],
      };

    case DELETE:
      return {
        vidgets: state.vidgets.filter(({ id }) => id !== action.payload),
      };

    default:
      return state ?? INIT_VIDGET;
  }
};
