import axios from 'axios';

import { CONFIG } from '@constants/config';
import { IUserInfo } from './redux/admin/admin.constants';

export const checkAdminJWT = async (jwt: string): Promise<boolean> =>
  new Promise((resolve) => {
    axios({
      method: 'GET',
      url: `${CONFIG.API}/admin`,
      headers: {
        Authorization: jwt,
      },
    })
      .then((res) => {
        if (res.data.data) {
          resolve(true);
        } else {
          resolve(false);
        }
      })
      .catch(() => resolve(false));
  });

export const getUsers = async (jwt: string): Promise<IUserInfo[]> =>
  (
    await axios({
      method: 'GET',
      url: `${CONFIG.API}/admin`,
      headers: {
        Authorization: jwt,
      },
    })
  ).data.data;

export const deleteUser = async (jwt: string, id: string) =>
  (
    await axios({
      method: 'DELETE',
      url: `${CONFIG.API}/admin/${id}`,
      headers: {
        Authorization: jwt,
      },
    })
  ).data.data;
