import { Button, Grid, Paper, Typography } from '@material-ui/core';
import { FC } from 'react';

import { Loader } from '@components/loader';
import { DialogModal, IDialogModal } from './dialog-modal';

import { PROMISES } from '@constants/promises';

import { useDialogStyles } from './dialog.styles';

interface IDialogPrompt extends IDialogModal {
  title: string;
  description: string;
  handleSubmit: () => void;
}

export const DialogPrompt: FC<IDialogPrompt> = ({
  title,
  description,
  handleClose,
  handleSubmit,
  isOpen,
}) => {
  const classes = useDialogStyles();

  return (
    <DialogModal isOpen={isOpen} handleClose={handleClose}>
      <Paper>
        <Loader id={PROMISES.DIALOG_PROMPT} height={145} width={242}>
          <Paper className={classes.prompt}>
            <Typography gutterBottom variant="h4" component="h2">
              {title}
            </Typography>
            <Typography gutterBottom variant="body2" component="p">
              {description}
            </Typography>
            <Grid container justify="space-between">
              <Grid item>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleSubmit}
                >
                  Yes
                </Button>
              </Grid>
              <Grid item>
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleClose}
                >
                  No
                </Button>
              </Grid>
            </Grid>
          </Paper>
        </Loader>
      </Paper>
    </DialogModal>
  );
};
