import { VIDGET_PERIOD } from '@screens/dashboard/components';
import {
  getDays, getDaysOfMonth, getDaysOfWeek, getMonths, getMonthsNames,
  getPredictMonth, getPredictWeek, getPredictYear
} from './format';

export const getLabels = (period: string) => {
  switch (period) {
    case VIDGET_PERIOD[1]:
      return getDaysOfMonth(getDays(30));
    case VIDGET_PERIOD[2]:
      return getMonthsNames(getMonths(12));
    default:
      return getDaysOfWeek(getDays(7));
  }
};

export const getPredictLabels = (period: string) => {
  switch (period) {
    case VIDGET_PERIOD[1]:
      return getPredictMonth();
    case VIDGET_PERIOD[2]:
      return getPredictYear();
    default:
      return getPredictWeek();
  }
};

