import { Button } from '@material-ui/core';
import { Add } from '@material-ui/icons';
import { FC } from 'react';

import { usePlusButtonStyles } from './plus-button.styles';

interface IPlusButton {
  onClick: () => void;
}

export const PlusButton: FC<IPlusButton> = ({ onClick }) => {
  const classes = usePlusButtonStyles();

  return (
    <Button
      onClick={onClick}
      className={classes.button}
      variant="contained"
      color="primary"
    >
      <Add fontSize="large" />
    </Button>
  );
};
