import { action, TActionCombiner } from '../utils';

import { ADMIN_ACTIONS, IAdmin, IUserInfo } from './admin.constants';

const { LOGIN, LOGOUT, USERS, DELETE, ADD } = ADMIN_ACTIONS;

export const AdminActions = {
  login: (admin: IAdmin) => action(LOGIN, admin),
  logout: () => action(LOGOUT),
  getUsers: (users: IUserInfo[]) => action(USERS, users),
  delete: (id: string) => action(DELETE, id),
  add: (user: IUserInfo) => action(ADD, user),
};

export type TAdminActions = ReturnType<TActionCombiner<typeof AdminActions>>;
