import { AddRecord } from '@components/add-record';
import { DataTable } from '@components/data-table';
import { ScreenTitle } from '@components/screen-title';

import { useAction, useReduxSelector } from '@services/redux';
import { IncomeActions } from '@services/redux/income';

import { useIncomeStyles } from './income.styles';

export const Income = () => {
  const { incomes } = useReduxSelector((redux) => redux.income);
  const handleDelete = useAction(IncomeActions.delete);
  const handleEdit = useAction(IncomeActions.update);
  const classes = useIncomeStyles();

  return (
    <div className={classes.screen}>
      <ScreenTitle title="Income" />
      <AddRecord actionAdd={IncomeActions.add} isIncome />
      <DataTable
        data={incomes}
        handleDelete={handleDelete}
        handleEdit={handleEdit}
        isIncome
      />
    </div>
  );
};
