import { useCallback, useEffect } from 'react';
import { trackPromise } from 'react-promise-tracker';
/* eslint-disable react-hooks/exhaustive-deps */
import { useLocation } from 'react-router';
import { v4 as uuidv4 } from 'uuid';

import { useAction, useReduxSelector } from './redux';
import { AdminActions } from './redux/admin';
import { IncomeActions } from './redux/income';
import { OutcomeActions } from './redux/outcome';
import { SettingActions } from './redux/setting';
import { UserActions } from './redux/user';

import { checkAdminJWT, getUsers } from './admin.api';
import { getRecords } from './records.api';
import { checkJWT, getSettings, getUserInfo } from './user.api';

import { Currency } from '@constants/currency';
import { Language } from '@constants/language';
import { PROMISES } from '@constants/promises';

export const getUUID = () => uuidv4();

export const useQuery = () => new URLSearchParams(useLocation().search);

export const getCurrency = (currency: Currency) => {
  switch (currency) {
    case Currency.USD:
      return 'USD';
    case Currency.UAH:
      return 'UAH';
    case Currency.EUR:
      return 'EUR';
  }
};

export const getLaunguage = (language: Language) => {
  switch (language) {
    case Language.UK:
      return 0;
    case Language.EN:
      return 1;
  }
};

export const parseCurrency = (currency?: string) => {
  switch (currency) {
    case 'UAH':
      return Currency.UAH;
    case 'EUR':
      return Currency.EUR;
    default:
      return Currency.USD;
  }
};

export const parseLanguage = (language?: number) => {
  switch (language) {
    case 0:
      return Language.UK;
    default:
      return Language.EN;
  }
};

export const useUser = () => {
  const { user } = useReduxSelector((redux) => redux.user);

  const login = useAction(UserActions.login);
  const logout = useAction(UserActions.logout);
  const get = useAction(SettingActions.get);
  const setIncomes = useAction(IncomeActions.set);
  const setOutcomes = useAction(OutcomeActions.set);

  const fetchData = useCallback(async () => {
    if (user) {
      if (await checkJWT(user.jwt)) {
        login(await getUserInfo(user.jwt));
        get(await getSettings(user.jwt));
        const records = await getRecords(user.jwt);
        const incomes = records
          .filter(({ isIncome }) => isIncome)
          .map((el) => ({ ...el, currency: parseCurrency(el.currency) }));
        setIncomes(incomes);
        const outcomes = records
          .filter(({ isIncome }) => !isIncome)
          .map((el) => ({ ...el, currency: parseCurrency(el.currency) }));
        setOutcomes(outcomes);
      } else {
        logout();
      }
    }
  }, []);

  useEffect(() => {
    trackPromise(fetchData(), PROMISES.USER);
  }, []);
};

export const useAdmin = () => {
  const { admin } = useReduxSelector((redux) => redux.admin);

  const logout = useAction(AdminActions.logout);
  const users = useAction(AdminActions.getUsers);

  const fetchData = useCallback(async () => {
    if (admin) {
      if (await checkAdminJWT(admin.jwt)) {
        users(await getUsers(admin.jwt));
      } else {
        logout();
      }
    }
  }, []);

  useEffect(() => {
    trackPromise(fetchData(), PROMISES.ADMIN);
  }, []);
};
