import { action, TActionCombiner } from '../utils';

import { OUTCOME_ACTIONS } from './outcome.constants';

const { ADD, UPDATE, DELETE, SET } = OUTCOME_ACTIONS;

export const OutcomeActions = {
  add: (outcome: IRecord) => action(ADD, outcome),
  update: (outcome: IRecord) => action(UPDATE, outcome),
  delete: (id: string) => action(DELETE, id),
  set: (outcomes: IRecord[]) => action(SET, outcomes)
};

export type TOutcomeActions = ReturnType<TActionCombiner<typeof OutcomeActions>>;
