import { FC } from 'react';

import { DialogRecord } from '@components/dialog-modal';
import { PlusButton } from '@components/plus-button';

import { useAddRecordState } from './add-record.state';

interface IAddRecord {
  actionAdd: any;
  isIncome: boolean;
}

export const AddRecord: FC<IAddRecord> = ({ actionAdd, isIncome }) => {
  const {
    isModalOpen,
    title,
    description,
    value,
    setTitle,
    setDescription,
    setValue,
    currency,
    setCurrency,

    date,
    handleDateChange,

    handleCloseModal,
    handleSave,
    handleOpenModal,

    error,
    handleFocus,
  } = useAddRecordState(actionAdd, isIncome);

  return (
    <>
      <PlusButton onClick={handleOpenModal} />
      <DialogRecord
        isOpen={isModalOpen}
        title={title}
        description={description}
        value={value}
        setTitle={setTitle}
        setDescription={setDescription}
        setValue={setValue}
        handleClose={handleCloseModal}
        currency={currency}
        setCurrency={setCurrency}
        handleSave={handleSave}
        date={date}
        handleDateChange={handleDateChange}
        error={error}
        handleFocus={handleFocus}
      />
    </>
  );
};
